const fragmentShaderText = require("./shaders/fragment.glsl").default
const vertexShaderText = require("./shaders/vertex.glsl").default

let canvas = document.querySelector("#c")
let gl = canvas.getContext("webgl")
if(!gl){ throw new Error("No WebGl")}
console.log("Successfully got context")

const startDate = new Date();
const startTime = startDate.getTime();

function seconds_elapsed (){
    var date_now = new Date ();
    var time_now = date_now.getTime ();
    var time_diff = time_now - startTime;
    var seconds_elapsed = time_diff / 1000;

    return ( seconds_elapsed ); 
}
function resizeCanvasToDisplaySize(canvas) {
  // Lookup the size the browser is displaying the canvas in CSS pixels.
  const displayWidth  = canvas.clientWidth;
  const displayHeight = canvas.clientHeight;
 
  // Check if the canvas is not the same size.
  const needResize = canvas.width  !== displayWidth ||
                     canvas.height !== displayHeight;
 
  if (needResize) {
    // Make the canvas the same size
    canvas.width  = displayWidth;
    canvas.height = displayHeight;
  }
 
  return needResize;
}
function createShader(gl, type, source){
   let shader = gl.createShader(type);
   gl.shaderSource(shader,source);
   gl.compileShader(shader)
   let success = gl.getShaderParameter(shader, gl.COMPILE_STATUS)
   if(success){
    return shader
   } 
   console.log(gl.getShaderInfoLog(shader))
   gl.deleteShader(shader)
}
var vertexShaderSource = vertexShaderText
var fragmentShaderSource = fragmentShaderText
var vertexShader = createShader(gl, gl.VERTEX_SHADER, vertexShaderSource)
var fragmentShader = createShader(gl, gl.FRAGMENT_SHADER, fragmentShaderSource)

function createProgram(gl, vertexShader, fragmentShader){
    let program = gl.createProgram();
    gl.attachShader(program, vertexShader)
    gl.attachShader(program, fragmentShader)
    gl.linkProgram(program)
    let success = gl.getProgramParameter(program, gl.LINK_STATUS)
    if(success){
        return program
    }
    console.log(gl.getProgramInfoLog(program))
    gl.deleteProgram(program)    
}
let program = createProgram(gl, vertexShader, fragmentShader)
let positionAttributeLocation = gl.getAttribLocation(program, "a_position")
let positionBuffer = gl.createBuffer()
let resolutionUniformLocation = gl.getUniformLocation(program, "u_resolution")
let colorUniformLocation = gl.getUniformLocation(program, "u_color")
let translationUniformLocation = gl.getUniformLocation(program, "u_translation")
let rotationLocation = gl.getUniformLocation(program, "u_rotation")
let scaleLocation = gl.getUniformLocation(program, "u_scale")
let timeLocation = gl.getUniformLocation(program, "u_time")

function randomInt(range){
    return Math.floor(Math.random() * range);
}

function setRectangle(gl, x, y, width, height){
    let x1 = x;
    let x2 = x + width;
    let y1 = y;
    let y2 = y + height;
    let positions = [
        x1, y1,
        x2, y1,
        x1, y2,
        x1, y2,
        x2, y1,
        x2, y2
    ]
    gl.bufferData(
        gl.ARRAY_BUFFER, 
        new Float32Array(positions), 
        gl.STATIC_DRAW
    )
}

gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer)
setGeometry(gl);

let translation = [0, 0];
let width = 100;
let height = 30;
let color = [Math.random(), Math.random(), Math.random(), 1];
let rotation = [0,1]
let scale = [1,1]
drawScene();

// Setup a ui.
webglLessonsUI.setupSlider("#x", {slide: updatePosition(0), max: gl.canvas.width });
webglLessonsUI.setupSlider("#y", {slide: updatePosition(1), max: gl.canvas.height});
webglLessonsUI.setupSlider("#rotation", {slide: updateRotation, max: 360});
webglLessonsUI.setupSlider("#scaleX", {value: scale[0], slide: updateScale(0), min: -5, max: 5, step: 0.01, precision: 2});
webglLessonsUI.setupSlider("#scaleY", {value: scale[1], slide: updateScale(1), min: -5, max: 5, step: 0.01, precision: 2});
// const handleMouseMove = event => {
//     translation[0] = event.clientX
//     translation[1] = event.clientY
//     // console.log("translation", translation)
//     drawScene() 
// }
// document.onmousemove = handleMouseMove

function updatePosition(index) {
    return function(event, ui) {
        translation[index] = ui.value;
        drawScene();
    };
}
function updateScale(index) {
    return function(event, ui) {
        scale[index] = ui.value;
        drawScene();
    };
}
function updateRotation(event, ui){
    let angleInDegrees = 360 - ui.value;
    let angleInRadians = angleInDegrees * Math.PI / 180;
    rotation[0] = Math.sin(angleInRadians)
    rotation[1] = Math.cos(angleInRadians)
    drawScene()
}
function setGeometry(gl) {
    gl.bufferData(
        gl.ARRAY_BUFFER,
        new Float32Array([
            // left column
            0, 0,
            30, 0,
            0, 150,
            0, 150,
            30, 0,
            30, 150,
    
            // top rung
            30, 0,
            100, 0,
            30, 30,
            30, 30,
            100, 0,
            100, 30,
    
            // middle rung
            30, 60,
            67, 60,
            30, 90,
            30, 90,
            67, 60,
            67, 90,
        ]),
        gl.STATIC_DRAW);
}
function drawScene(){
    resizeCanvasToDisplaySize(gl.canvas)
    gl.viewport(0,0,gl.canvas.width, gl.canvas.height)
    gl.clearColor(0,0,0,0)
    gl.clear(gl.COLOR_BUFFER_BIT)
    gl.useProgram(program)
    gl.enableVertexAttribArray(positionAttributeLocation)
    gl.bindBuffer(gl.ARRAY_BUFFER, positionBuffer)

    let size = 2
    let type = gl.FLOAT
    let normalize = false
    let stride = 0
    let offset = 0 
    gl.vertexAttribPointer(
        positionAttributeLocation, 
        size,
        type,
        normalize,
        stride,
        offset
        )
    gl.uniform2f(resolutionUniformLocation, gl.canvas.width, gl.canvas.height)
    gl.uniform4fv(colorUniformLocation, color)
    gl.uniform2fv(translationUniformLocation, translation);
    gl.uniform2fv(rotationLocation, rotation)
    gl.uniform2fv(scaleLocation, scale)
    gl.uniform1f(timeLocation, seconds_elapsed())
    // offset = 0
    // count = 18
    // gl.drawArrays(primitiveType, offset, count)
    for ( let ii = 0; ii < 50; ++ii){
    setRectangle(
        gl, randomInt(100), randomInt(100),randomInt(500),randomInt(500)
        // gl, 0, 0, window.innerWidth, window.innerHeight
    )
     // Draw the rectangle.
    var primitiveType = gl.TRIANGLES;
    // var offset = 0;
    var count = 6;
    gl.drawArrays(primitiveType, offset, count);
}    
        
}
function draw(timestamp){
    drawScene()
    window.requestAnimationFrame(draw);
}

window.requestAnimationFrame(draw);
